import React, { Component } from 'react';
import nutriendo_empresa from '../../../../assets/nutriendo_empresa.jpeg';
import ListContent from '../ListContent';
import './Colaborations.css';

class Colaborations extends Component {

  getDetails() {
    return (
      <>
        <p>Si eres una empresa comprometida con la salud y el bienestar de tus trabajadores, esto te interesa:</p>
        <div className='actions'>
          <img src={ nutriendo_empresa }></img>
        </div>
      </>
    );
  }

  getData() {
    const data = {
      'Valoración, supervisión y  elaboración de menús': this.getDetails(),
      'Asesoramiento y colaboraciones periódicas': this.getDetails()
    }
    return data;
  }

  render() {
    return (
      <div className='colaborations-container'>
      <div className='title'><p>¿Te interesa recibir/organizar alguna formación sobre nutrición?</p><p>¿Quieres profundizar en un tema específico?</p></div>
        <ListContent data={ this.getData() } fixed='up' />
      </div>
    );
  }
}

export default Colaborations;
