import React, { Component } from 'react';
import ListContent from '../ListContent';
import './Workshops.css';

class Workshops extends Component {

  getData() {
    const data = {
      'Formaciones': this.getDetail(),
      'Charlas y talleres': this.getDetail(),
      'Conferencias': this.getDetail()
    };
    return data;
  }

  getDetail() {
    return (
      <>
        <p>Destinadas a:</p>
        <ul>
          <li>público general</li>
          <li>asociaciones</li>
          <li>centros y empresas...</li>
        </ul>
        <p>Con temáticas, contenido, metodología y duración adaptada a los intereses y objetivos deseados</p>
      </>
    );
  }

  render() {
    return (
      <div className='workshop-container'>
        <div className='title'>
          <p>¿Te interesa recibir/organizar alguna formación sobre nutrición?</p>
          <p>¿Quieres profundizar en un tema específico?</p>
        </div>
        <ListContent data={ this.getData() } fixed='up' />
      </div>
    );
  }
}

export default Workshops;
